:root {
    --main-bg-color: #1E1E1E;
    --main-fg-color: #353535;
    --title-color: white;
    --text-color: #e9e9e9;
    --main-column-width: min(max(620px,50vw),75vw);
    --main-column-padding: 5vw;
}
body {
    margin:0px;
}
.main-wrapper{
    z-index: 3000;
    position: relative;
    width: 100%;
    background: var(--main-bg-color);
    height: 100%;
}
.center-column-wrapper{
    z-index: 3000;
    width:  var(--main-column-width);
    margin-left:  auto;
    margin-right:  auto;
    background: var(--main-bg-color);
    padding: 10px 0vw 10px 0vw;
}
.left-column-wrapper {
    position:absolute;
    left:0;
    top:0;
    right:50%;
    margin-right:calc(var(--main-column-width)/2);
    height: 100%;
}
.right-column-wrapper {
    position:absolute;
    left:50%;
    top:0;
    right:0;
    margin-left:calc(var(--main-column-width)/2);
    height: 100%;
}
.side-column-wrapper{
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-evenly;
}
.content-wrapper {
    background: var(--main-fg-color);
    margin:0px 5vw 0px 5vw;
    padding: 20px 40px 20px 40px;
    border: 10px;
    border-radius: 10px;
}
h1.content {  
    text-align: left;
    margin:0px;
    color:var(--title-color);
}
p.content {  
    text-align: left;
    margin:0px;
    color:var(--text-color);
}
hr.rounded {
    border-top: 2px solid #bbb;
    border-radius: 3px;
}