.main-wrapper {
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.content-wrapper {
    background: var(--main-fg-color);
    margin:0px 5vw 0px 5vw;
    padding: 20px 40px 20px 40px;
    border: 10px;
    border-radius: 10px;
}
h1.content {  
    text-align: left;
    margin:0px;
    color:var(--title-color);
}
hr.rounded {
    border-top: 2px solid #bbb;
    border-radius: 3px;
}
img.content {
    width: 100%;
}